@font-face {
  font-family: LabelFont;
  src: url("./assets/ImpactLabelReversed.ttf");
}

@font-face {
  font-family: Oswald;
  src: url("https://fonts.googleapis.com/css?family=Oswald");
}

.App {
  text-align: center;
  max-width: 100vw;
  height: 100vh;
  color: #d8dee9;
  display: flex;
  flex-direction: column;
}

#root {
  max-width: 100vw;
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  background-color: #3b4252;
}

.active {
  background-color: #3b4252;
}

.parallax {
  /* The image used */
  background-image: url("./assets/nyhavn-min.jpg");
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

html,
body,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
caption {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  overflow-x: hidden;
}

.menu-bar {
  display: grid;
  justify-items: center;
  align-items: center;
  grid-template-columns: 1fr 1fr 1fr;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html {
  background: white;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.header-component {
  background-color: rgba(0, 0, 0, 0);
  z-index: 99;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  max-height: 15vh;
  flex-grow: 0;
  display: grid;
  grid-template-columns: 0.3fr 1fr 1fr;
  align-items: center;
  color: black;
}

.logo-img-container {
  position: absolute;
  top: 2%;
  left: 2%;
  opacity: 0.9;
}
.logo-img-container:hover {
  opacity: 1;
  transform: scale(1.05);
}

.logo-img {
  padding-top: 16px;
  max-width: 100px;
  max-height: 16vh;
}

.person-name {
  padding-left: 2%;
  text-decoration: none;
  align-self: center;
  text-align: left;
  color: white;
  font-size: 1.5em;
}

.person-name:hover {
  text-decoration: none;
  color: white;
}

a {
  text-decoration: none;
  color: #000;
}

a:visited {
  text-decoration: none;
  color: #000;
}

a:hover {
  text-decoration: none;
  color: #000;
}

a:focus {
  text-decoration: none;
  color: #000;
}

a:hover,
.navBtn:active {
  text-decoration: none;
  color: #000;
}

.footer-component {
  padding: 8px;
  width: 100%;
  z-index: 99;
  flex-grow: 0;
  background-color: #323131;
}
.footer-component .footer-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 0;
  padding-bottom: 0;
}

.person-info {
  padding-left: 16px;
  padding-top: 0.2%;
  margin-bottom: 0;
  display: grid;
  grid-template-columns: 1fr;
  text-align: left;
  color: white;
  font-size: 1em;
}

.social-section {
  padding-top: 6px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center;
  align-items: center;
}

.social-logo {
  max-width: 18%;
}

.card-title {
  padding: 16px 16px 0 16px;
  margin: 0;
  text-align: left;
  font-size: 1.2em;
}

.card-contents {
  display: grid;
  grid-template-columns: 1fr;
}

.card-item {
  line-height: 1.5;
  text-align: left;
  margin: 16px;
  padding: 0;
  font-size: 1em;
}

.card-component {
  transition: max-height 0.5s ease-out;
  position: relative;
  align-self: start;
  margin: 2vh 1vw 1vh 1vw;
  color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background-color: #3b4252;
  display: grid;
  grid-template-columns: 1fr;
  max-height: 100vh;
}
.card-component.preview {
  max-height: 30vh;
}
.card-component .touch-close {
  position: absolute;
  top: 4px;
  right: 4px;
  font-size: 14px;
}
.card-component .preview-content {
  position: relative;
  height: 100%;
}
.card-component .preview-content .preview-image-container {
  position: relative;
  height: 100%;
}
.card-component .preview-content .preview-image-container .preview-image {
  opacity: 0.9;
  margin: 16px;
  max-width: 50%;
}

.empty-card-component {
  min-height: 10vh;
  position: relative;
  align-self: start;
  margin: 2vh 1vw 1vh 1vw;
  color: white;
  display: grid;
  grid-template-columns: 1fr;
}

.info-item {
  font-size: 0.8em;
  padding: 6px;
  margin: 0;
}

.list-card-item-icon {
  align-self: center;
  justify-self: center;
  max-width: 24px;
  margin: 0 16px;
  flex-grow: 0;
}

.list-card-item-text {
  text-align: left;
  padding-top: 5px;
}

.list-card-item {
  margin: 12px 3px;
  display: flex;
  flex-direction: row;
}

#right-text {
  text-align: right;
}

.CV-section {
  display: flex;
  flex-direction: column;
}
.CV-section .skills-header {
  padding-top: 8px;
  font-size: 3em;
  font-family: Oswald;
  font-weight: bold;
}

.landing-page-role-text {
  font-family: LabelFont;
  white-space: nowrap;
  text-align: left;
  color: white;
  font-size: 3vw;
  padding-left: 16px;
}

.scroll-down-section {
  width: 100%;
  position: absolute;
  height: 100vh;
  align-items: center;
  justify-items: center;
}

.scroll-down-section.animated {
  animation-duration: 2.5s;
  animation-iteration-count: infinite;
}

.scroll-logo {
  transform: translate(-50%, -50%);
  position: absolute;
  top: 75%;
  opacity: 0.8;
  max-width: 3%;
}

#breathing-logo {
  width: 270px;
  padding: 20px;
  margin: 50px auto;
  font-size: 24px;
  color: #fff;
  -webkit-font-smoothing: antialiased;
  text-align: center;
}

.text-and-scrolling-text {
  display: grid;
  grid-template-columns: 1fr 1fr;
  overflow: hidden;
  min-height: 60px;
}

a {
  text-decoration: none;
  color: #111b47;
}

.animation-show {
  transition: all 0.75s ease;
  opacity: 1;
}

/*** Content ***/
.slider-container {
  margin: 0;
  padding: 0;
  max-height: 40px;
}

.slider-container p {
  margin: 0;
  line-height: 150%;
}

/*** target hooks ****/
@-webkit-keyframes slide-animation {
  0% {
    opacity: 0;
  }
  2% {
    opacity: 1;
  }
  20% {
    left: 0px;
    opacity: 1;
  }
  22.5% {
    opacity: 0.6;
  }
  25% {
    left: -600px;
    opacity: 1;
  }
  45% {
    left: -600px;
    opacity: 1;
  }
  47.5% {
    opacity: 0.6;
  }
  50% {
    left: -1200px;
    opacity: 1;
  }
  70% {
    left: -1200px;
    opacity: 1;
  }
  72.5% {
    opacity: 0.6;
  }
  75% {
    left: -1800px;
    opacity: 1;
  }
  95% {
    opacity: 1;
  }
  98% {
    left: -1800px;
    opacity: 0;
  }
  100% {
    left: 0px;
    opacity: 0;
  }
}

#slider ul {
  -webkit-animation: slide-animation 8s infinite;
}

.slider {
  margin-top: 0;
  padding-top: 0;
}

/* use to paused the content on mouse over */
#slider ul:hover {
  -moz-animation-play-state: paused;
  -webkit-animation-play-state: paused;
}

.history-component {
  margin-bottom: 32px;
}
.history-component .card-item {
  padding: 6px;
  margin: 0px;
}

.history-component-title {
  font-size: 3em;
  padding: 32px 0;
}

.job-and-education-grid {
  display: grid;
  grid-template-columns: 1fr 0.25fr 1fr;
}

.timeline {
  background: url("./assets/Timeline Arrow.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.education {
  margin: 0 42px;
}
.education .history-card-component {
  margin-right: 6px;
}

.job {
  margin: 0 42px;
}
.job .history-card-component {
  margin-left: 6px;
}

.history-card-component {
  margin: 12px 16px 10px 16px;
  position: relative;
  align-self: start;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background-color: #3b4252;
  display: grid;
  grid-template-columns: 1fr;
}

.history-card-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.history-card-title {
  padding-top: 2px;
  align-self: center;
  justify-self: center;
  text-align: center;
  width: 100%;
  height: 100%;
  background-color: #3b4252;
}

.history-card-content {
  padding: 2%;
  background-color: white;
  color: black;
}

.history-card-left {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 0.2fr 1fr;
}

.history-card-icon {
  padding: 2%;
  width: 30%;
}

.history-card-right {
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
  justify-items: center;
}

.job {
  margin-top: 8%;
}

.education {
  margin-top: 8%;
  align-items: right;
}

.come-in {
  transform: translateY(150px);
  animation: come-in 1s ease forwards;
}

.come-in:nth-child(odd) {
  animation-duration: 0.6s;
  /* So they look staggered */
}

@keyframes come-in {
  to {
    transform: translateY(0);
  }
}

.animation-hide {
  opacity: 0;
  pointer-events: none;
}

/* Position and sizing of burger button */
.bm-burger-button {
  position: absolute;
  width: 36px;
  height: 30px;
  right: 36px;
  top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: rgba(255, 255, 255, 0.8);
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/* General sidebar styles */
.bm-menu {
  background: rgba(49, 55, 55, 0.7);
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
  padding-bottom: 0;
  height: calc(100% - 2em) !important;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

.menu-item {
  padding: 10% 2% 10% 2%;
  color: white;
}

.menu-item {
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  text-transform: uppercase;
  text-decoration: none;
  color: white;
  margin: 0 15px;
  font-size: 16px;
  letter-spacing: 1px;
  position: relative;
  display: inline-block;
}

.menu-item:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background: white;
  top: 47%;
  animation: out 0.2s cubic-bezier(1, 0, 0.58, 0.97) 1 both;
}

.menu-item:hover:before {
  animation: in 0.2s cubic-bezier(1, 0, 0.58, 0.97) 1 both;
}

@keyframes in {
  0% {
    width: 0;
    left: 0;
    right: auto;
  }
  100% {
    left: 0;
    right: auto;
    width: 100%;
  }
}

@keyframes out {
  0% {
    width: 100%;
    left: auto;
    right: 0;
  }
  100% {
    width: 0;
    left: auto;
    right: 0;
  }
}

@keyframes show {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.menu-item:nth-child(1) {
  animation: show 0.2s 1.1s ease 1 both;
}

.menu-item:nth-child(2) {
  animation: show 0.2s 1.2s ease 1 both;
}

.menu-item:nth-child(3) {
  animation: show 0.2s 1.3s ease 1 both;
}

.menu-item:nth-child(4) {
  animation: show 0.2s 1.4s ease 1 both;
}

.menu-item:nth-child(5) {
  animation: show 0.2s 1.5s ease 1 both;
}

.shader-overlay {
  background: rgba(35, 35, 35, 0.5);
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  height: 100%;
  flex-grow: 1;
}

.landing-page-logo {
  position: relative;
  text-align: center;
  color: white;
}

.landing-page-text {
  padding: 1%;
  opacity: 0.9;
  background-color: transparent;
  position: relative;
  width: 90%;
  text-align: center;
  color: white;
}

.landing-page-text-header {
  font-family: LabelFont;
  font-size: 2em;
}

.landing-page-text-subtitle {
  text-align: right;
  font-size: 3vw;
  font-family: LabelFont;
}

.mikkel-logo {
  max-width: 75%;
  opacity: 0.25;
}

.landing-page-text {
  margin-top: 25%;
  font-size: 2em;
}

.landing-page-contents {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  grid-template-columns: 1fr;
  width: 100vw;
  height: 100vh;
}

.cv-card-grid {
  margin: 0 42px;
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: 1fr 1fr;
}
.cv-card-grid .card-item {
  margin: 16px;
  padding: 0;
}
.cv-card-grid .landing-page-hero-mobile {
  display: none;
}

.card-inner-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.hero-img {
  padding: 0 16px;
  max-width: 80%;
}

.hero-img-text {
  color: white;
}

.contact-text {
  color: white;
  align-self: center;
}

.contact-component {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.contact-component .contact-header {
  position: relative;
  padding-top: 66px;
  font-family: Oswald;
  font-size: 5em;
  color: white;
  text-align: center;
}
.contact-component .contact-parallax {
  /* The image used */
  background-image: url("./assets/mermaid-min.jpg");
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
}
.contact-component .contact-grid {
  margin-top: 66px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.contact-component .contact-grid .mail-section input,
.contact-component .contact-grid .mail-section textarea {
  border: none;
  border-radius: 4px;
}
.contact-component .contact-section {
  margin-top: 16px;
  margin-right: 42px;
  justify-self: center;
}
.contact-component .contact-section .icon-and-text {
  display: grid;
  grid-template-columns: 0.3fr 1fr;
  background: #535353;
  opacity: 0.8;
}
.contact-component .contact-section .icon-and-text:first-child {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}
.contact-component .contact-section .icon-and-text:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}
.contact-component .mail-section {
  margin-left: 42px;
  margin-top: 16px;
  align-self: center;
  justify-self: center;
  width: 80%;
  display: grid;
  grid-template-columns: 1fr;
}
.contact-component .contact-icon {
  margin: 2%;
  padding: 5%;
  align-self: center;
  justify-self: center;
  max-width: 35%;
}
.contact-component .contact-input {
  margin-top: 16px;
  padding: 12px;
  color: black;
}
.contact-component .contact-input:first-child {
  margin-top: 0;
}
.contact-component .message-input {
  margin-top: 2%;
  text-align: left;
  padding: 2%;
}
.contact-component .send-button {
  cursor: pointer;
  transition: all 0.25s;
  background-color: #3b4252;
  border: none;
  outline: none;
  color: #d8dee9;
  font-size: 1.2em;
  margin: 5% 2% 2% 2%;
  padding: 2%;
}
.contact-component .send-button:hover {
  background-color: #434c5e;
}

.spacer-bar {
  margin: 5%;
  min-height: 2px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.contact-bottom-spacer {
  min-height: 8vh;
}

.projects-grid {
  padding: 0 66px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.projects-grid .card-component {
  overflow: hidden;
  text-overflow: ellipsis;
  display: flex;
  flex-direction: column;
}

.projects-header {
  position: relative;
  padding-top: 66px;
  font-family: Oswald;
  font-size: 5em;
  color: white;
  text-align: center;
}

.projects-component {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.projects-component .projects-parallax {
  display: flex;
  flex-direction: column;
  /* The image used */
  background-image: url("./assets/storm_trooper.jpg");
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  flex-grow: 1;
  height: 100%;
}
.projects-component .projects-content {
  position: relative;
  display: flex;
  flex-direction: column;
}
.projects-component .double-card-grid {
  display: grid;
  grid-template-columns: 1fr;
}

.spacer {
  min-height: 13vh;
}

.card-overlay-button {
  position: absolute;
  max-width: 6%;
  color: white;
  font-size: 1.2em;
  display: grid;
  grid-template-columns: 1fr;
  text-align: center;
  border: none;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 0.5s linear;
  justify-items: center;
  align-items: right;
  bottom: 1%;
  right: 0.5%;
}

.card-component:hover .card-overlay-button {
  visibility: visible;
  opacity: 1;
}

.overlay-button-icon {
  max-width: 90%;
  opacity: 0.8;
}

@media screen and (max-width: 800px) {
  .text-and-scrolling-text {
    display: flex;
    flex-direction: column;
  }
  .text-and-scrolling-text p {
    margin-left: 0;
    text-align: center;
  }
  .logo-img {
    max-width: 58px;
    margin-top: 12px;
    margin-left: 16px;
  }
  .landing-page-role-text,
  .landing-page-text-subtitle {
    padding-top: 16px;
    padding-left: 0;
    font-size: 18px;
  }
  .scroll-logo {
    max-width: 8%;
  }
  .logo-img-container {
    left: 0;
  }
  .cv-card-grid {
    margin: 0 24px;
  }
  .list-card-item {
    margin: 8px 3px;
  }
  .list-card-item .list-card-item-icon {
    max-width: 16px;
    margin-right: 8px;
  }
  .list-card-item .list-card-item-text {
    font-size: 14px;
  }
  .cv-card-grid {
    grid-template-columns: 1fr;
  }
  .cv-card-grid .landing-page-hero {
    display: none;
  }
  .cv-card-grid .landing-page-hero-mobile {
    display: block;
    padding-top: 6px;
  }
  .cv-card-grid .landing-page-hero-mobile .hero-img-text {
    padding-top: 6px;
  }
  .CV-section {
    height: auto;
  }
  .contact-component {
    height: 91vh;
  }
  .contact-component .contact-grid {
    padding-bottom: 42px;
    grid-template-columns: 1fr;
  }
  .contact-component .contact-section {
    display: none;
  }
  .projects-component .projects-grid {
    grid-template-columns: 1fr;
    padding: 0 24px;
  }
  .projects-component .projects-grid .card-title {
    font-size: 16px;
  }
  .projects-component .projects-grid .card-item {
    font-size: 14px;
  }
  .history-component {
    height: auto;
    min-height: auto;
  }
  .history-component .history-card-grid {
    grid-template-columns: 1fr;
  }
  .history-component .history-component-title {
    padding: 8px 6px;
  }
  .history-component .timeline {
    background-position: initial;
  }
  .history-component .education,
